@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

nav {
  display: -webkit-flex;
  display: flex;
  border-bottom: 2px white solid;
  min-height: 50px;
  list-style: none;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

nav > li {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  margin-top: 12px;
  margin-left: 100px;
  margin-bottom: 10px;
}

img {
  width: 30px;
  height: 30px;
  background-color: white;
  padding: 2px;
  border-radius: 2px;
}

nav > li:nth-child(2) {
  margin-left: 100px;
}

nav > li:nth-child(3) {
  margin-left: auto;
  margin-right: 100px;
}

nav > li > a {
  text-decoration: none;
  font-weight: bold;
  color: white;
}

nav > li > a:hover {
  background-color: white;
  border-radius: 2px;
  padding-right: 20px;
  padding-left: 20px;
  color: black;
}

#homeWrapDiv {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 120px;
  left: 150px;
  top: 400px;
}

#mainPara {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 2.7rem;
  font-weight: bold;
  color: white;
}

#subPara {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.3rem;
  color: white;
  margin-top: 15px;
}

#homeWrapDiv > a >  button {
  font-size: 0.9rem;
  font-weight: bold;
  padding: 15px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 30px;
}

.shopWrapDiv {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 150px;
  left: 100px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 300px;
}

.prodListDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 50px;
  font-size: 1.3rem;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 150px;
}

.shopMainDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100vh;
}

.prodDiv {
  margin-left: 150px;
  margin-bottom: 50px;
}

.imageClass {
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
}

.productInfoPara, .productPrice {
  font-size: 1.2rem;
}

.productInfoPara {
  margin-bottom: 10px;
}

.cartWrapperDiv {
  position: absolute;
  right: 0px;
  padding-left: 20px;
  padding-right: 10px;
  padding-top: 10px;
  background-color: whitesmoke;
  height: 100vh;
  width: 350px;
  border-left: black 2px solid;
}

.endBtn {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 1.5rem;
  font-family:Arial, Helvetica, sans-serif ;
}

.nameOfSect {
  margin-top: 90px;
  margin-bottom: 50px;
}

.nameOfSect > p {
  font-size: 1.5rem;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.addedProd {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 130px;
}

.addedProdImg {
  width: 100px;
  height: 100px;
}

.priceDetail {
  margin-left: 20px;
  margin-top: 10px;
}

.nameOfProd {
  font-size: 1.2rem;
  margin-top: 4px;
  margin-bottom: 8px;
  font-family: Arial, Helvetica, sans-serif;
}

.countBox {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  margin-left: 10px;
  font-size: 1.2rem;
  text-align: center;
}

.reduceProd {
  width: 20px;
  height: 20px;
  font-size: 1.2rem;
  font-family: monospace;
  background-color: black;
  color: white;
  border: black solid 1px;
  font-weight: bold;
}

.addMoreProd {
  width: 20px;
  height: 20px;
  font-size: 1.2rem;
  font-family: monospace;
  background-color: black;
  color: white;
  border: black solid 1px;
  font-weight: bold;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
}

input[type=number] {
  -moz-appearance: textfield;
}

input:invalid {
  border: 1px dashed red;
}

.subTotal {
  display: -webkit-flex;
  display: flex;
  margin-top: 20px;
  font-size: 1.2rem;
}

.subTotal > span {
  margin-right: 10px;
}

.checkOut {
  position: absolute;
  left: 60px;
  margin-top: 30px;
  font-size: 1.5rem;
  padding: 20px;
  background: white;
  border: 1px solid brown;
}

.checkOut:hover {
  background-color: black;
  color: white;
  border: 2px solid yellow;
}
